.sign-in,
.signup {
    padding: var(--space-xxxl) var(--space-md);
    background-color: var(--bg-primary);
    background-image: radial-gradient(circle at 10% 20%, rgba(104, 112, 250, 0.05) 0%, transparent 40%),
        radial-gradient(circle at 90% 80%, rgba(76, 206, 172, 0.05) 0%, transparent 40%);
    color: var(--text-primary);
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    width: 100%;
    max-width: 1000px;
    background: var(--bg-surface);
    margin: 0 auto;
    box-shadow: 0 8px 32px var(--shadow-color);
    border-radius: var(--radius-xl);
    padding: var(--space-xxl);
    border: 1px solid var(--border-color);
    transition: all var(--transition-slow);
    position: relative;
    overflow: hidden;
}

.container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background: var(--grad-primary);
}

.container>p {
    text-align: center;
    color: var(--text-secondary);
    margin-bottom: var(--space-xl);
    font-size: var(--font-size-sm);
    font-weight: var(--font-weight-medium);
}

.form-title {
    margin-bottom: var(--space-xl);
    font-weight: var(--font-weight-extrabold);
    font-size: var(--font-size-2xl);
    color: var(--color-primary);
    text-align: left;
    letter-spacing: -0.02em;
}

/* Input Modernization */
.form-group {
    position: relative;
    margin-bottom: var(--space-lg);
}

.form-group label {
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
    color: var(--text-muted);
    font-size: 20px;
    transition: all var(--transition-base);
}

.form-group input {
    width: 100%;
    display: block;
    background: var(--bg-secondary);
    border: 2px solid var(--border-color);
    border-radius: var(--radius-md);
    padding: 14px 16px 14px 45px;
    color: var(--text-primary);
    font-size: var(--font-size-base);
    transition: all var(--transition-base);
}

.form-group input:focus {
    outline: none;
    border-color: var(--color-primary);
    background: var(--bg-surface);
    box-shadow: 0 0 0 4px rgba(104, 112, 250, 0.1);
}

.form-group input:focus+label {
    color: var(--color-primary);
}

/* Button Modernization */
button[type="submit"],
.form-submit {
    width: 100%;
    background: var(--grad-primary);
    color: #fff;
    border: none;
    padding: 16px;
    border-radius: var(--radius-md);
    cursor: pointer;
    font-size: var(--font-size-base);
    font-weight: var(--font-weight-bold);
    transition: all var(--transition-base);
    box-shadow: 0 4px 12px var(--button-shadow);
    margin-top: var(--space-md);
}

button[type="submit"]:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 8px 20px var(--button-shadow);
}

button[type="submit"]:active {
    transform: translateY(0);
}

.action-buttons-container {
    display: flex;
    justify-content: space-between;
    gap: var(--space-md);
    margin-top: var(--space-lg);
    padding-top: var(--space-md);
    border-top: 1px solid var(--border-color);
}

button[type="button"] {
    background: transparent;
    border: none;
    color: var(--color-primary);
    cursor: pointer;
    font-size: var(--font-size-sm);
    font-weight: var(--font-weight-bold);
    padding: 8px;
    text-decoration: none;
    transition: all var(--transition-base);
}

button[type="button"]:hover {
    color: var(--color-primary-dark);
    opacity: 0.8;
}


button[type="button"]:hover:not(:disabled) {
    color: #2980b9;
    text-decoration: none;
}

button[type="button"]:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* Modal styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    padding: 20px;
    overflow-y: auto;
}

.modal-content {
    background: var(--bg-card, #ffffff);
    color: var(--text-primary, #333);
    padding: 35px;
    border-radius: 16px;
    width: 100%;
    max-width: 500px;
    max-height: 90vh;
    overflow-y: auto;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
    animation: modalSlideIn 0.3s ease;
}

@keyframes modalSlideIn {
    from {
        opacity: 0;
        transform: translateY(-30px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Password strength indicator */
.password-strength {
    margin-top: 12px;
    margin-bottom: 15px;
}

.strength-bar {
    height: 5px;
    background: #e0e0e0;
    margin-bottom: 8px;
    border-radius: 3px;
    transition: all 0.3s ease;
}

.strength-text {
    font-size: 13px;
    font-weight: 600;
}

.password-strength.strong .strength-bar {
    background: linear-gradient(90deg, #2ecc71 0%, #27ae60 100%);
}

.password-strength.strong .strength-text {
    color: #2ecc71;
}

.password-strength.medium .strength-bar {
    background: linear-gradient(90deg, #f1c40f 0%, #f39c12 100%);
}

.password-strength.medium .strength-text {
    color: #f39c12;
}

.password-strength.weak .strength-bar {
    background: linear-gradient(90deg, #e67e22 0%, #d35400 100%);
}

.password-strength.weak .strength-text {
    color: #e67e22;
}

.password-strength.invalid .strength-bar {
    background: linear-gradient(90deg, #e74c3c 0%, #c0392b 100%);
}

.password-strength.invalid .strength-text {
    color: #e74c3c;
}

/* Password requirements */
.password-requirements {
    font-size: 12px;
    color: var(--text-secondary, #666);
    margin-top: 15px;
    padding: 12px;
    background: var(--bg-secondary, #f8f9fa);
    border-radius: 8px;
}

.password-requirements p {
    margin: 0 0 8px 0;
    font-weight: 600;
}

.password-requirements ul {
    margin: 0;
    padding-left: 20px;
    list-style: none;
}

.password-requirements li {
    margin: 5px 0;
    position: relative;
    padding-left: 20px;
    transition: color 0.2s ease;
}

.password-requirements li:before {
    content: "○";
    position: absolute;
    left: 0;
    color: #ccc;
}

.password-requirements li.valid {
    color: #2ecc71;
    font-weight: 600;
}

.password-requirements li.valid:before {
    content: "✓";
    color: #2ecc71;
}

/* Responsive Design */
@media screen and (max-width: 992px) {

    .signin-content,
    .signup-content {
        flex-direction: column;
    }

    .signin-image,
    .signin-form {
        width: 100%;
    }

    .signin-image {
        order: 2;
        margin-top: 30px;
    }

    .signin-form {
        order: 1;
    }
}

@media screen and (max-width: 768px) {

    .sign-in,
    .signup {
        padding: 40px 15px;
    }

    .container {
        padding: 30px 20px;
        border-radius: 12px;
    }

    .form-title {
        font-size: 24px;
        margin-bottom: 25px;
    }

    .modal-title {
        font-size: 20px;
    }

    .modal-content {
        padding: 25px 20px;
    }

    .action-buttons-container {
        flex-direction: column;
        align-items: stretch;
    }

    button[type="button"] {
        text-align: center;
        width: 100%;
    }
}

@media screen and (max-width: 480px) {

    .sign-in,
    .signup {
        padding: 30px 10px;
    }

    .container {
        padding: 25px 15px;
    }

    .form-title {
        font-size: 20px;
    }

    .form-group input,
    .form-group textarea {
        font-size: 14px;
        padding: 8px 30px 8px 28px;
    }

    button[type="submit"],
    .form-submit {
        padding: 12px 20px;
        font-size: 15px;
    }

    .signin-image {
        display: none;
    }
}